
import axios from 'axios'
import XHeader from 'vux/src/components/x-header/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: { XHeader, GroupTitle, Flexbox, FlexboxItem, XButton },
data() {
return {
partyDetail:''

}
},
methods: {
handle(id){
console.log('11111111111111',id)

this.$router.push({
path: '/party/information',
name: 'information',

params: {


departmentid:id

},query: {

departmentid:id
}

})
}




,
getDepartment(){
axios({
method: 'get',
url: '/dangjian/pdepartment/getList',

}) .then((res)=> {
this.partyDetail=res.data
console.log(res.data)
}).catch(function (error) {
console.log(error);
})
}

},mounted() {
this.getDepartment()
},


};



